.home-header {
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.8);
  position: relative;
  overflow: hidden;

  video {
    position: absolute;
    right: 0;
    top: 0;
    min-width: 100%;
    min-height: 100%;
    width: auto;
    height: auto;
    z-index: -1;
    background-color: #000000;
  }


  @import "./HomeHeaderTetris.scss";
  @import "./HomeScrollMouse.scss";
  @import "./HomeScrollArrow.scss";
  .scroll-thing {
	  visibility: hidden;
    opacity: 0;
    &.active {
		opacity: 1;
		visibility: visible;
		transition: 2s;
    }
  }
}


@import "./HomeAboutMe.scss";
@import './HomeWhatIDo.scss';
@import './HomeWorks.scss';